body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.wcu {
    font-family: 'Ubuntu';
}

/* wcu Box */
.wcu-phone-wrapper {
    display: inline-block;
    position: relative;
    width: 100%;
}

.wcu-phone-wrapper:before {
    content: '+375';
    font-size: 16px;
    color: black;
    position: absolute;
    left: 12px;
    top: 11px;
}

.wcu-phone-input {
    display: block;
    text-indent: 54px;
    border: 1px solid #b6b6b6;
    background: #d0d0d0;
    padding: 10px 0;
    width: 100%;
    margin-bottom: 5px;
    font-size: 16px;
    outline: none;
}

.wcu-box {
    display: none;
    background: #e9e9e9;
    color: #717171;
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 300px;
    padding: 0 30px;
    box-sizing: border-box;
    z-index: 200;
}

.wcu-box-close {
    position: absolute;
    top: 20px;
    right: 15px;
    cursor: pointer;
}

.wcu-box-close::after,
.wcu-box-close::before {
    content: '';
    display: block;
    width: 32px;
    height: 5px;
    background-color: #888585;
    transition: background 0.2s;
    position: relative;
}

.wcu-box-close::after {
    transform: rotate(-45deg);
}

.wcu-box-close::before {
    transform: rotate(45deg);
    top: 5px;
}

.wcu-box-label {
    font-size: 24px;
    font-weight: 300;
    margin-top: 37vh;
    margin-bottom: 10px;
}

.wcu-button-send {
    display: block;
    background: #f2645a;
    border: 1px solid #b9544d;
    border-radius: 3px;
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
    color: white;
    cursor: pointer;
}

/* wcu Show Button */
.wcu-show-btn {
    position: fixed;
    bottom: 40px;
    right: 40px;
    box-sizing: border-box;
    z-index: 100;
}

.wcu-show-btn .wcu-circle {
    background-image: url("data:image/svg+xml;base64,");
    border: 3px solid #d33d3d;
    background-color: #a31a1a;
    border-radius: 50%;
    width: 70px;
    height: 70px;
    background-size: 100%;
    box-sizing: border-box;
    cursor: pointer;
}
.overlay {
    display: none;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(255, 255, 255, .5);
}
